import SearchLink from '../../components/SearchLink'
export default {
  components: {
    SearchLink
  },
  data () {
    return {
      // 默认选中一级分类里面的第一个
      activeIndex: 0,
      categories: []
    }
  },
  onLoad () {
    this.getCategories()
  },
  methods: {
    // 跳转到搜索列表
    toSearchList (catName) {
      wx.navigateTo({ url: '/pages/search_list/main?keyword=' + catName })
    },
    async getCategories () {
      this.categories = await this.$request({
        url: '/api/public/v1/categories'
      })
    }
  }
}
